/**
 * 
 * @module $ui/utils/queue
 * @author 
 */
import Events from './events'

const taskFunc = function (data) {
  return Promise.resolve(data)
}

/**
 *    [Events]{@link module:utils/events~Events}
 * @extends Events
 *
 * @example
 *
 *     let queue = new Queue(data => {
 *       return new Promise((resolve, reject) => {
 *          setTimeout(() => {
 *           resolve('ok')
 *          }, 1000)
 *        })
 *       }).start()
 *       queue.push(1)
 *       queue.push(2)
 */
class Queue extends Events {
  /**
   * 
   * @param {Function} task Promise
   * @param {Array} list 
   */
  constructor(task, list = []) {
    super()
    /**
     * 
     * @type {Array}
     * @private
     */
    this.__list__ = list
    /**
     * 
     * @type {boolean}
     */
    this.waiting = false
    /**
     * 
     * @type {boolean}
     */
    this.running = false
    
    
    /**
     * Promise
     * @type {Function}
     */
    this.task = task || taskFunc
    
    
  }
  
  /**
   * 
   * @param {*} data 
   * @returns {Queue}
   */
  push(data) {
    if (Array.isArray(data)) {
      this.__list__ = this.__list__.concat(data)
    } else {
      this.__list__.push(data)
    }
    this.run()
    return this
  }
  
  /**
   *  
   */
  run() {
    if (!this.running || this.waiting || this.__list__.length === 0) return
    this.waiting = true
    const data = this.__list__.shift()
    this.task(data).then(res => {
      this.waiting = false
      /**
       * 
       * @event module:utils/queue~Queue#success
       * @param {*} res resolve
       * @param {*} data 
       */
      this.$emit('success', res, data)
      if (this.__list__.length > 0) {
        this.run()
      } else {
        /**
         * 
         * @event module:utils/queue~Queue#complete
         */
        this.$emit('complete', this)
      }
    }).catch(e => {
      this.waiting = false
      /**
       * 
       * @event module:utils/queue~Queue#error
       * @param {*} e reject
       * @param {*} data 
       */
      this.$emit('error', e, data, this)
    })
    return this
  }
  
  /**
   * 
   */
  clear() {
    this.__list__ = []
    this.waiting = false
    return this
  }
  
  /**
   * 
   * @returns {Queue}
   */
  start() {
    this.running = true
    this.run()
    return this
  }
  
  /**
   * 
   * @returns {Queue}
   */
  stop() {
    this.running = false
    return this
  }
  
  /**
   * 
   */
  destroy() {
    super.destroy()
    this.stop()
    this.clear()
  }
}

/**
 * Queue
 * @export
 */
export default Queue
