/**
 * vuex store 
 * @module store/index
 */

import state from './state'
import mutations from './mutations'
import getters from './getters'
import modules from '$my/code/modules'

/**
 * Vuex.Store 
 */
export default {
  strict: process.env.NODE_ENV !== 'production',
  /**
   *  state  [store/state]{@link module:store/state} 
   * @type {Object}
   */
  state,
  /**
   * modules,  import modules from '@/base/modules'
   * @type {Object}
   */
  modules,
  /**
   * mutations,    [store/mutations]{@link module:store/mutations} 
   * @type {Object}
   */
  mutations,
  /**
   * getters  [module:store/getters]{@link module:store/getters} 
   * @type {Object}
   */
  getters
}
