/**
 * 
 */

import {
  Vue,
  Vuex,
  Router,
  progress,
  Access,
  registerMicroApps,
  start,
  setDefaultMountApp,
  runAfterFirstMounted,
  loadMicroApp
} from './enhance'

import uiConfig from '$ui/config'

export {
  Vue,
  Vuex,
  Router,
  progress,
  Access
}


/**
 * 
 * @param {Array|string[]} apps {name,container,activeRule,entry,mode}
 * @return {*}
 */
export function parseAppConfig(apps = []) {
  return apps.map(item => {
    const app = typeof item === 'string' ? {name: item, mode: 'hash'} : item
    return {
      container: '#app-container',
      activeRule: activeRule(app.name, app.mode),
      entry: app.entry ? app.entry : `/${app.name}/`,
      ...app
    }
  })
}

/**
 * 
 * @param {string} appName 
 * @param {string} mode  hash  history
 * @return {function(*): (*|boolean)}
 */
export function activeRule(appName, mode = 'hash') {
  // single-spa location
  return location => {
    if (mode === 'hash') {
      // return _app && _app.$route.path.startsWith(`/${appName}`)
      return location.hash.startsWith(`#/${appName}`)
    }
    return location.pathname.startsWith(`/${appName}`)
  }
}

/**
 * 
 * @param {object} params
 * @param {string} params.el 
 * @param {Vuex} params.store vuex
 * @param {VueRouter} params.router 
 * @param {AppMaster} params.AppMaster 
 * @param {Array} params.apps 
 * @param {Array} params.access 
 */
export function appRender({el, store, router, AppMaster, apps, access}) {
  return new Vue({
    el: el,
    components: {
      AppMaster
    },
    store,
    router,
    access,
    data() {
      return {
        isMatchApp: false
      }
    },
    watch: {
      $route: {
        immediate: true,
        handler(val) {
          this.isMatchApp = !!apps.find(app => app.activeRule(window.location))
        }
      }
    },
    render(h) {
      const props = {
        isMatchApp: this.isMatchApp,
        ...uiConfig.scale
      }
      return h(AppMaster, {props})
    }
  })
}

/**
 * 
 * @param {object} params
 * @param {string} params.el 
 * @param {Vuex} params.store vuex
 * @param {VueRouter} params.router 
 * @param {AppMaster} params.AppMaster 
 * @param {Array} params.apps 
 * @param {Object} [lifeCycles] {beforeLoad, beforeMount, afterMount, beforeUnmount, afterUnmount}
 */
export function registerApps(params = {}, lifeCycles) {
  const apps = params.apps || []
  // 
  window.__POWERED_BY_MY_APPS__ = true
  registerMicroApps(apps, lifeCycles)
}

/**
 * views
 * @function getView
 * @param {string} path vueviews
 * @return {Promise} promise
 *
 * @example
 * //  views/index.vue 
 *  getView('index')
 */
export const getView = require('$ui/import/view/' + process.env.NODE_ENV)

/**
 * 
 * @function appStart
 * @param {Object} opts 
 * @param {boolean} [opts.prefetch=true] Whether to prefetch assets of sub apps after first sub app mounted
 * @param {boolean} [opts.jsSandbox=true] While sandbox enabled, we could guarantee that sub apps is isolated with each others
 * @param {boolean} [opts.singular=true] Only one sub app display at one runtime, that means a sub app will wait to mount until the before one unmounted
 * @param {Function} [opts.fetch=window.fetch] Set a custom fetch function
 */
export const appStart = start

/**
 * app
 * @function setDefaultApp
 * @param {string} appName 
 */
export const setDefaultApp = appName => setDefaultMountApp(`/${appName}`)

/**
 * 
 * @function loadApp
 * @param {string|HTMLElement} el 
 * @param {string|object} app 
 * @param {object} configuration  
 */
export const loadApp = (el, app, configuration) => {
  const appMessage = parseAppConfig([app])[0]
  appMessage.container = el
  return loadMicroApp(appMessage, configuration)
}

/**
 * 
 * @function firstAppMounted
 * @param {function} effect
 */
export const firstAppMounted = runAfterFirstMounted


