/**
 * Mockjs
 * @module $ui/utils/mock
 * @author 
 */

import Mock from 'mockjs'
import pathToRegex from 'path-to-regexp'
import {parse, getParams} from './url'
import {log, tip} from './log'
import config from '$ui/config'


// 
Mock.setup(config.mock)

/**
 * 
 * @private
 * @param {string} str
 * @returns {Object} json
 *
 *
 * @example
 * // json 
 *  "{name:'kenny', password:'123'}"
 *
 *  // 
 *  {name:'kenny', password:'123'}
 */
function toObject(str) {
  if (typeof str === 'object') {
    return str
  }
  const regex = /^\{[\W\w]*\}$/
  return regex.test(str) ? JSON.parse(str) : null
}


/**
 *  body  JSONjson
 * @private
 * @param {String} body 
 * @returns {Object} json
 *
 * @example
 * // json 
 *  "{name:'kenny', password:'123'}"
 *
 *  //
 *  name=kenny&passwrod=123
 *
 *  // 
 *  {name:'kenny', password:'123'}
 */
function getBody(body) {
  if (!body) return null
  
  const regex = /^\{[\W\w]*\}$/
  return regex.test(body) ? JSON.parse(body) : parse(body)
}




/**
 * mock
 * @param {Object} item mock
 * @param {String} item.title 
 * @param {String} item.url url
 * @param {String} item.method get post head put patch delete options
 * @param {Object} [item.params] url
 * @param {Object|Function} item.template 
 *
 * @example
 *
 * import mock from '@/utils/mock'
 * mock({
 *  title: 'GET_USERS',
 *  url: GET_USERS,
 *  method: 'get',
 *  params: {},
 *  template: {
 *    code: 0,
 *    msg: '',
 *    data: {
 *      'id': '@guid',
 *      'name': '@cname'
 *    }
 *   }
 * })
 */
export default function (item = {}) {
  if (!item.url) return
  
  // url
  const oRegex = pathToRegex(item.url)
  // path
  const regex = new RegExp(
    oRegex.source
      .replace('(?:\\/)?$', '($|\\?)')
      .replace(/\[\^\d\\\/\]/, '[^\\/]'),
    'i')
  // ur
  
  Mock.mock(regex, item.method, function (options) {
    let result
    const body = getBody(options.body) || {}
    // GETPOST
    const query = Object.assign({},
      item.params || {},
      getParams(item.url, options.url),
      parse(options.url || ' '),
      body,
      toObject(body.data) || {}
    )
    if (typeof item.template === 'function') {
      result = item.template(Mock, options.url, query, options)
    } else {
      // 
      let templateString = JSON.stringify(item.template)
      
      // 
      for (const key in query) {
        templateString = templateString.replace(new RegExp('{{' + key + '}}', 'g'), query[key])
      }
      
      // 
      result = Mock.mock(JSON.parse(templateString))
    }
    // 
    tip('Mock', item.title)
    log(options, result)
    log('-------------------------------------------')
    return result
  })
}
