/**
 * 
 * @module $ui/utils/log
 * @author chenhuachun
 */

import globalConfig from '../config'

// 
const colors = {
  default: '#35495E',
  primary: '#3488ff',
  success: '#43B883',
  warn: '#e6a23c',
  error: '#f56c6c'
}

/**
 *  console 
 * @private
 * @param {string} name 
 * @param {Array} args 
 */
function applyMethod(name, args) {
  const debug = globalConfig.debug
  if (!debug) return;
  if (!window) return;
  if (!window.console) return;
  
  const method = window.console[name]
  try {
    method.apply(window.console, args)
  } catch (e) {
  
  }
  
}

/**
 * 
 */
export function log() {
  applyMethod('log', arguments)
}

/**
 * 
 * @param {...string}  
 */
export function warn() {
  applyMethod('warn', arguments)
}

/**
 * 
 * @param {...string}  
 */
export function error() {
  applyMethod('error', arguments)
}

/**
 * 
 */
export function info() {
  applyMethod('info', arguments)
}

/**
 * 
 * @param {...string}  
 */
export function dir() {
  applyMethod('dir', arguments)
}

/**
 * 
 * @param {string} title 
 * @param {string} info 
 * @param {string} [type=primary] primary/success/warn/error
 */
export function tip(title, info, type = 'primary') {
  log(
    `%c ${title} %c ${info} %c`,
    'background:#35495E; padding: 1px; border-radius: 3px 0 0 3px; color: #fff;',
    `background:${colors[type]}; padding: 1px; border-radius: 0 3px 3px 0;  color: #fff;`,
    'background:transparent'
  )
}

/**
 * log
 * @return {log}
 */
export function create() {
  const methods = {
    warn,
    error,
    tip,
    dir,
    info
  }
  const _log = log
  Object.assign(_log, methods)
  return _log
}

