/**
 * 
 */

const merge = require('lodash/merge')

const __config__ = {
  /**
   * debug 
   */
  debug: process.env.NODE_ENV !== 'production',

  /**
   * 
   */
  appName: null,

  /**
   * pathhash
   */
  routePrefix: null,

  /**
   * 
   */
  router: {
    /**
     *  : hash / history
     */
    mode: 'hash',
    /**
     *  /app/  base  "/app/"
     */
    base: '/'
  },

  /**
   * 
   */
  autoRoutes: true,

  /**
   * Vuex
   */
  vuex: {
    /**
     *  mutation  Vuex state 
     */
    strict: process.env.NODE_ENV !== 'production'
  },
  /**
   * 
   */
  keys: {
    code: 'code',
    data: 'data',
    message: 'msg',
    total: 'total',
    list: 'list',
    page: 'page',
    limit: 'limit',
    pages: 'pages'
  },

  // 
  statusCode: {
    // 
    success: 0,

    // , 
    notLogin: 401,

    // 
    authorize: 403,

    // 
    error: 500
  },

  /**
   * axios 
   */
  axios: {
    /**
     * 
     */
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    },
    /**
     * 
     */
    timeout: 0,
    /**
     * 
     */
    withCredentials: true,
    /**
     * 
     */
    responseType: 'json',
    /**
     * 
     */
    maxContentLength: -1
  },
  /**
   * svg
   */
  svg: {
    viewBox: '0 0 1024 1024',
    width: '1em',
    height: '1em',
    fill: 'currentColor'
  },
  access: {},
  mock: {
    timeout: '100-500'
  },
  /**
   * 
   */
  scale: {
    // 
    width: 1920,
    // 
    height: 1080,
    // 
    disabled: true
  },
  /**
   * 
   */
  analysis: true,
  version: '4.0.0-88'
}

export default __config__


export function set(config) {
  merge(__config__, config || {})
}
